<template>
  <div>
    <b-row>
      <b-col
        cols="12"
        sm="12"
        md="12"
        lg="12"
        xl="12"
        class="mb-2"
      >
        <b-badge
          :variant="isConnected? 'success': 'danger'"
        >
          <feather-icon
            :icon="isConnected? 'ZapIcon': 'ZapOffIcon'"
            class="mr-25"
          />
          <span>{{ isConnected? 'Conectado': 'Desconectado' }}</span>
        </b-badge>
      </b-col>
      <b-col
        cols="12"
        sm="12"
        md="12"
        lg="12"
        xl="12"
      >
        <LeafletBasic
          ref="mapa"
          style="height: 400px;"
        />
      </b-col>
      <b-col
        cols="12"
        sm="12"
        md="12"
        lg="12"
        xl="12"
        class="mt-1"
      >
        <alerts-table
          :list-query="listQuery"
          :alerts="alerts"
          :is-loading="isLoading"
          @handle-location="(location) => $refs.mapa.setLocation(location)"
          @handle-change-dates="handleChangeDates"
          @handle-alert-details="(id) => $refs.alertDetailsRef.getById(id)"
        />
      </b-col>
      <AlertDetails ref="alertDetailsRef" />
    </b-row>
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import {
  BRow,
  BCol,
  BBadge,
} from 'bootstrap-vue'
import UseAlertsWS from './composables/useAlertsWS'
import AlertsTable from './components/AlertsTable.vue'
import LeafletBasic from './components/LeafletBasic.vue'
import AlertDetails from './components/AlertDetails.vue'

export default {
  components: {
    AlertsTable,
    LeafletBasic,
    BRow,
    BCol,
    BBadge,
    AlertDetails,
  },
  created() {
    this.$store.commit('verticalMenu/UPDATE_VERTICAL_MENU_COLLAPSED', true)
  },
  destroyed() {
    this.$store.commit('verticalMenu/UPDATE_VERTICAL_MENU_COLLAPSED', false)
  },
  setup() {
    const mapa = ref()
    const alertDetailsRef = ref()

    const {
      isConnected,
      isLoading,
      listQuery,
      alerts,
      handleChangeDates,
    } = UseAlertsWS(8, mapa)

    return {
      isConnected,
      isLoading,
      listQuery,
      alerts,
      mapa,
      handleChangeDates,
      alertDetailsRef,
    }
  },
}
</script>

