import {
  onMounted,
  reactive,
  ref,
  watch,
} from '@vue/composition-api'
import { useToast } from 'vue-toastification/composition'
import ToastificationContentVue from '@/@core/components/toastification/ToastificationContent.vue'

import { Manager } from 'socket.io-client'
import _ from 'lodash'
import { Howl } from 'howler'

import AlertService from '@/services/AlertService'

const UseAlertsWS = (perPage = 10, refMapa) => {
  const title = 'Alertas'
  const URL_SOCKET = `${process.env.VUE_APP_BASE_URL}/socket.io/socket.io.js`
  const manager = new Manager(URL_SOCKET)
  const socket = manager.socket('/alerts')
  const isConnected = ref(false)
  const toast = useToast()
  const isLoading = ref(false)
  const alerts = ref([])
  const alertDetails = ref({
    alert_id: '',
    alert_address: '',
    device_name: '',
    typeDevice_name: '',
    unit_plate: '',
    driver_name: '',
    driver_last_name: '',
    driver_license: '',
    driver_cellphone: '',
    driver_photo: '',
    model_name: '',
    brand_name: '',
    created_at: '',
  })
  const showSidebar = ref(false)

  const listQuery = reactive({
    take: perPage,
    page: 1,
    order: 'DESC',
    pageCount: 1,
    itemCount: 0,
    search: '',
    date_created: [],
    sortBy: 'id',
    from: 0,
    to: 0,
  })

  const sound = ref(null)
  sound.value = new Howl({
    // eslint-disable-next-line global-require
    src: [require('@/assets/sounds/alarm-1.mp3')], // Asegrate de que la ruta sea correcta
    // html5: true, // Opcional: permite la reproduccin en streaming
  })

  const fetchList = async () => {
    isLoading.value = true
    try {
      const response = await AlertService.fetchList(listQuery)
      const { data, meta } = response.data
      const {
        take,
        page,
        pageCount,
        itemCount,
        from,
        to,
      } = meta
      listQuery.itemCount = Number(itemCount)
      listQuery.pageCount = Number(pageCount)
      listQuery.take = Number(take)
      listQuery.from = Number(from)
      listQuery.to = to > data.length ? itemCount : to
      listQuery.page = page
      alerts.value = data
      isLoading.value = false
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContentVue,
        props: {
          title: `Error al cargar ${title}`,
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getById = async id => {
    try {
      isLoading.value = true
      const resp = await AlertService.getById(id)
      Object.assign(alertDetails.value, resp.data)
      isLoading.value = false
      showSidebar.value = true
    } catch (error) {
      isLoading.value = false
    }
  }

  const reload = _.debounce(() => {
    fetchList()
  }, 500)

  watch(() => listQuery.take, () => fetchList())

  watch(() => listQuery.page, () => fetchList())

  watch(() => listQuery.search, () => {
    reload()
  })

  watch(() => listQuery.date_created, () => {
    reload()
  })

  const addListeners = () => {
    socket.on('connect', () => {
      isConnected.value = true
    })

    socket.on('disconnect', () => {
      isConnected.value = false
    })

    socket.on('new-alert', alert => {
      alerts.value = alerts.value.map(a => {
        const { isNew, ...rest } = a
        return { ...rest }
      })
      sound.value.play()
      setTimeout(() => {
        sound.value.stop()
      }, 5000)
      alerts.value.unshift(alert)
      refMapa.value.setLocation(alert.location)
    })
  }

  const dateStringToFormatYMDH = dateString => {
    const dateArray = dateString.split('-')
    dateArray.reverse()
    const year = dateArray[0].trim()
    const month = dateArray[1].trim().padStart(2, '0')
    const day = dateArray[2].trim().padStart(2, '0')
    const dateFormat = `${year}-${month}-${day}`
    return dateFormat
  }

  const handleChangeDates = (selectedDates, dateStr) => {
    if (dateStr === '') {
      listQuery.date_created = []
      return
    }

    const arrayDates = dateStr.split('a')
    if (arrayDates.length === 1) {
      const f = dateStringToFormatYMDH(dateStr)
      listQuery.date_created = [f, f]
    } else {
      const f1 = dateStringToFormatYMDH(arrayDates[0])
      const f2 = dateStringToFormatYMDH(arrayDates[1])
      listQuery.date_created = [f1, f2]
    }
  }

  onMounted(() => {
    addListeners()
  })

  return {
    isLoading,
    listQuery,
    alerts,
    isConnected,
    handleChangeDates,
    alertDetails,
    getById,
    showSidebar,
  }
}

export default UseAlertsWS
