  methods: {
    click: e => { console.log(e) },
    ready: e => { console.log(e) },
  },
  setup() {
    const mapLeaflet = ref()
    const show = ref(false)
    const style = ref('normal.day.grey')
    const apiKey = ref('q9wf_iQvH_xa-W8fVJJGKV2M0g1WvPDQEVZh489fY-c')
    const url = ref(`https://2.base.maps.ls.hereapi.com/maptile/2.1/maptile/newest/${style.value}/{z}/{x}/{y}/512/png8?apiKey=${apiKey.value}&ppi=320`)
    // const url = ref('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png')

    const customicon = L.divIcon({
      className: 'leaflet-data-marker',
      html: svg.replace('#', '%23'),
      iconAnchor: [30, 50],
      iconSize: [36, 42],
      popupAnchor: [0, -50],
    })

    const iconAwsome = ref(customicon)

    const content = ref('')
    const marker = ref(null)
    const zoom = ref(13)
    const center = ref([19.433026740813453, -99.1332049])

    const bounds = ref([])
    const maxBounds = ref([])

    const setLocation = location => {
      bounds.value = []
      const [lng, lat] = location.coordinates
      const coordinates = L.latLng(lat, lng)
      marker.value = coordinates
      content.value = 'prueba'
      // center.value = [lat, lng]
      // zoom.value = 17
      // const bound = latLngBounds([...center.value, ...center.value])
      // bounds.value = bound
      // maxBounds.value = bound
      mapLeaflet.value.mapObject.flyTo(coordinates, 18)
      // $('.mePin').addClass('bounceInDown')

      setTimeout(() => {
        const miPin = document.querySelector('.leaflet-marker-pane')
        console.log({ miPin })
      }, 5000)
    }

    const some = () => {
      console.log('mapa', mapLeaflet.value.mapObject)
    }

    setTimeout(() => {
      some()
    }, 2000)
    setTimeout(() => {
      show.value = true
    }, 1000)

    return {
      show,
      style,
      apiKey,
      url,
      iconAwsome,
      marker,
      zoom,
      center,
      bounds,
      maxBounds,
      content,

      setLocation,
      mapLeaflet,
    }
  },
