<template>
  <b-sidebar
    bg-variant="white"
    right
    shadow
    :visible="showSidebar"
    @change="(val) => showSidebar = val"
  >
    <div>
      <div class="text-center">
        <!-- Avatar -->
        <b-avatar
          size="72px"
          class="mr-1"
          :src="alertDetails.driver_photo"
        />
        <!-- Name -->
        <b-card-text class="mt-2 h4 color-inherit text-reset">
          {{ alertDetails.driver_name }} {{ alertDetails.driver_last_name }}
        </b-card-text>
      </div>

      <b-list-group class="mt-1 rounded-0">
        <b-list-group-item
          class="bg-transparent"
        >
          <feather-icon
            class="align-text-bottom"
            icon="CreditCardIcon"
          />
          <span class="ml-1">
            Licencia: {{ alertDetails.driver_license }}
          </span>
        </b-list-group-item>
        <b-list-group-item
          class="bg-transparent"
        >
          <feather-icon
            class="align-text-bottom"
            icon="SmartphoneIcon"
          />
          <span class="ml-1">
            Celular: {{ alertDetails.driver_cellphone }}
          </span>
        </b-list-group-item>
      </b-list-group>
      <card-unit-details
        class="mt-2"
        :plate="alertDetails.unit_plate"
        :brand="alertDetails.brand_name"
        :model="alertDetails.model_name"
      />
      <b-list-group class="mt-1 rounded-0">
        <b-list-group-item
          class="bg-transparent"
        >
          <feather-icon
            class="align-text-bottom"
            icon="MapPinIcon"
          />
          <span class="ml-0">
            Direccin: {{ alertDetails.alert_address }}
          </span>
        </b-list-group-item>
        <b-list-group-item
          class="bg-transparent"
        >
          <feather-icon
            class="align-text-bottom"
            icon="CalendarIcon"
          />
          <span class="ml-0">
            Fecha: {{ dateTimeFormat(alertDetails.created_at) }}
          </span>
        </b-list-group-item>
      </b-list-group>
      <card-device-details
        class="mt-2"
        :name="alertDetails.device_name"
        :type="alertDetails.typeDevice_name"
      />
    </div>
  </b-sidebar>
</template>

<script>
import {
  BSidebar,
  BAvatar,
  BCardText,
  BListGroup,
  BListGroupItem,
} from 'bootstrap-vue'
import useCustomFunctions from '@/composables/useCustomFunctions'
import useAlertDetails from '../composables/useAlertDetails'
import CardUnitDetails from './CardUnitDetails.vue'
import CardDeviceDetails from './CardDeviceDetails.vue'

export default {
  components: {
    BSidebar,
    BAvatar,
    BCardText,
    BListGroup,
    BListGroupItem,
    CardUnitDetails,
    CardDeviceDetails,
  },
  setup() {
    const {
      alertDetails,
      showSidebar,
      getById,
    } = useAlertDetails()

    const {
      dateTimeFormat,
    } = useCustomFunctions()

    return {
      alertDetails,
      showSidebar,
      getById,
      dateTimeFormat,
    }
  },
}

</script>
