<template>
  <b-card
    no-body
    class="mb-0"
  >
    <b-overlay
      :show="isLoading"
      spinner-variant="primary"
      spinner-type="grow"
      spinner-small
      rounded="sm"
    >
      <div class="m-2">
        <!-- Table Top -->
        <b-row>
          <!-- Per Page -->
          <b-col
            cols="12"
            md="4"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Mostrar</label>
            <v-select
              v-model="listQuery.take"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="perPageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>{{ title }}</label>
          </b-col>

          <b-col
            cols="12"
            md="4"
          >
            <flat-pickr
              v-model="dates"
              class="form-control"
              placeholder="Bucar por rango de fechas"
              :config="{ mode: 'range', dateFormat: 'd-m-Y' }"
              input-id="rangoFechas"
              @on-close="(selectedDates, dateStr) =>
                $emit('handle-change-dates', selectedDates, dateStr)
              "
            />
          </b-col>

          <!-- Search -->
          <b-col
            cols="12"
            md="4"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="listQuery.search"
                class="d-inline-block mr-0 mr-md-0 mr-sm-0 mt-md-0 mt-sm-1"
                placeholder="Buscar..."
              />
              <slot name="plus">
                &nbsp;
              </slot>
            </div>
          </b-col>
        </b-row>
      </div>

      <b-table
        ref="refUsuariosListTable"
        class="position-relative"
        :items="alerts"
        responsive
        :fields="headerColumns"
        :tbody-tr-class="rowClass"
        primary-key="id"
        show-empty
        empty-text="No hay registros"
        outlined
        small
        hover
        sticky-header
        head-variant="'light'"
      >
        <!-- Column: createdAt -->
        <template #cell(createdAt)="data">
          <span
            class="font-weight-bolder"
            :class="data.item.isNew ? 'text-black' : ''"
          >
            {{ dateTimeFormat(data.item.createdAt) }}
          </span>
        </template>

        <!-- Column: address -->
        <template #cell(address)="data">
          <span
            class="font-weight-bolder"
            :class="data.item.isNew ? 'text-black' : ''"
          >
            {{ data.item.address }}
          </span>
        </template>

        <!-- Column: device.unit.plate -->
        <template #cell(device.unit.plate)="data">
          <span
            class="font-weight-bolder"
            :class="data.item.isNew ? 'text-black' : ''"
          >
            {{ data.item.device.unit.plate }}
          </span>
        </template>

        <!-- Column: Actions -->
        <template #cell(actions)="data">
          <div class="text-nowrap">
            <b-avatar
              size="48"
              variant="light-danger"
            >
              <feather-icon
                size="20"
                class="cursor-pointer"
                icon="MapPinIcon"
                @click="$emit('handle-location', data.item.location)"
              />
            </b-avatar>
            <b-avatar
              size="48"
              variant="light-info"
              class="ml-1"
            >
              <feather-icon
                size="20"
                class="cursor-pointer"
                icon="EyeIcon"
                @click="$emit('handle-alert-details', data.item.id)"
              />
            </b-avatar>
          </div>
        </template>
      </b-table>
      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">
              Mostrando {{ listQuery.from }} a {{ listQuery.to }} de
              {{ listQuery.itemCount }} {{ title }}
            </span>
          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >
            <b-pagination
              v-model="listQuery.page"
              :total-rows="listQuery.itemCount"
              :per-page="listQuery.take"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>
          </b-col>
        </b-row>
      </div>
    </b-overlay>
  </b-card>
</template>

<script>
import {
  BAvatar,
  BCard,
  BRow,
  BCol,
  BFormInput,
  BTable,
  BPagination,
  BOverlay,
} from 'bootstrap-vue'
import vSelect from 'vue-select'
import useCustomFunctions from '@/composables/useCustomFunctions'
import flatPickr from 'vue-flatpickr-component'
import { onMounted, ref } from '@vue/composition-api'

export default {
  components: {
    BAvatar,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BTable,
    BPagination,
    BOverlay,
    vSelect,
    flatPickr,
  },
  props: {
    alerts: {
      type: Array,
      required: true,
    },
    isLoading: {
      type: Boolean,
      required: true,
    },
    listQuery: {
      type: Object,
      required: true,
    },
  },
  setup(__, ctx) {
    const title = 'Alertas'
    const { dateTimeFormat } = useCustomFunctions()
    const perPageOptions = [8, 20, 50]
    const today = new Date()
    const todaytString = `${today.getDate()}-${today.getMonth() + 1}-${today.getFullYear()}`
    const datesToday = [today, today]
    const dates = ref(datesToday)
    onMounted(() => {
      ctx.emit('handle-change-dates', [], `${todaytString} a ${todaytString}`)
    })

    const headerColumns = [
      {
        key: 'createdAt',
        label: 'Fecha',
        sortable: true,
      },
      {
        key: 'address',
        label: 'Direccion',
        sortable: true,
        isRowHeader: false,
      },
      {
        key: 'device.unit.plate',
        label: 'Unidad',
        sortable: true,
        isRowHeader: true,
      },
      { key: 'actions', label: 'Acciones' },
    ]

    const rowClass = (item, type) => {
      let colorClass = ''
      if (item || type === 'row') {
        if (item.isNew) {
          console.log({ item })
          colorClass = 'table-danger'
        }
        return colorClass
      }
      return colorClass
    }

    return {
      title,
      perPageOptions,
      headerColumns,
      rowClass,
      // Extra Filters

      dateTimeFormat,
      dates,
    }
  },
}
</script>

<style lang="scss" scoped>
.per-page-selector {
  width: 90px;
}

.text-black {
  color: black !important;
}
</style>

<style lang="scss">
@import "@core/scss/vue/libs/vue-select.scss";
@import "@core/scss/vue/libs/vue-flatpicker.scss";
</style>
